@import "antd/dist/antd.css";
@import "~normalize.css";
@import "~@blueprintjs/core/lib/css/blueprint.css";
@import "~@blueprintjs/icons/lib/css/blueprint-icons.css";
@import "~@blueprintjs/datetime/lib/css/blueprint-datetime.css";
@font-face {
  font-family: "PTSansRegular";
  src: url("fonts/PTSansRegular.eot");
  src: url("fonts/PTSansRegular.eot") format("embedded-opentype"),
    url("fonts/PTSansRegular.woff2") format("woff2"),
    url("fonts/PTSansRegular.woff") format("woff"),
    url("fonts/PTSansRegular.ttf") format("truetype"),
    url("fonts/PTSansRegular.svg#PTSansRegular") format("svg");
}
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-height: 100vh;
}

.logo_container {
  max-height: 80px;
  width: 100vw;
  background-color: rgba(255, 183, 3, 0.8);
  border-bottom: 0.8px solid #023047;
}

.logo_name {
  text-align: center;
  font-size: 30px;
  font-weight: bold;
  line-height: 80px;
  font-family: "PTSansRegular";
  color: #023047;
  text-transform: uppercase;
  letter-spacing: 9px;
}

.menu {
  display: flex;
  justify-content: space-evenly;
  min-width: 400px;
  margin-bottom: 10px;
}

.menu_link {
  max-height: 50px;
  color: #023047;
  text-decoration: none;
  font-size: 16px;
  font-family: "PTSansRegular";
  text-align: center;
  line-height: 50px;
  letter-spacing: 1px;
  border: 0px solid transparent;
}

.menu_link:hover {
  text-decoration: underline;
}

.profile {
  display: flex;
  align-items: center;
  font-size: 16px;
}

.block_date_and_icon {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0px auto;
}

.date {
  font-family: "PTSansRegular";
  font-size: 15px;
  line-height: 15px;
  color: #023047;
  font-weight: bold;
  margin: 0 5px;
}

.block_icon {
  margin: 0 5px;
}

.mainBlock {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 100%;
  max-height: 330px;
  background-color: white;
  margin-top: 10px;
  padding: 10px;
}

.block_calendar {
  max-width: 241px;
  max-height: 330px;
  border: 0.1px solid rgba(142, 202, 230, 0.8);
}

.calendar:hover {
  background-color: rgba(142, 202, 230, 0.1);
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.calendar {
  font-family: "PTSansRegular";
  color: rgba(107, 112, 92, 100%);
  font-size: 12px;
  height: 100%;
}

.block_plans {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 510px;
  min-height: 296px;
  margin: 0px 40px;
  padding: 10px;
}

.block_plans:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.block_chart {
  max-width: 241px;
  height: 100%;
  border: 0.5px solid rgba(201, 176, 112, 0.8);
}

.block_chart:hover {
  background-color: rgba(201, 176, 112, 0.2);
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.chartPlanning {
  max-width: 300px;
  margin: 10px 20px;
}

.chart_description {
  color: #023047;
  font-weight: bold;
  font-family: "PTSansRegular";
}

.mainBlock_myDay {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  width: 100%;
  padding: 20px;
}

.mainBlock_myDay_plans {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  min-width: 572px;
  min-height: 377px;
  margin-left: 45px;
  padding-left: 15px;
}

.mainBlock_myDay_plans:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.mainBlock_myDay_charts {
  width: 45%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.myDay_charts_container {
  max-width: 300px;
}

.myDay_charts_container:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.chartPlanning p {
  margin-top: 5px;
  text-align: center;
  font-family: "PTSansRegular";
  font-size: 12px;
  color: grey;
}

.work:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.notWork:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.work {
  background-color: rgba(142, 202, 230, 1);
}

.notWork {
  background-color: rgba(255, 183, 3, 0.4);
}

.noTasks {
  font-weight: normal;
  text-align: center;
  color: rgba(107, 112, 92, 100%);
  font-size: 11px;
  font-family: "PTSansRegular";
}

.text {
  text-align: center;
  font-size: 12px;
  color: #023047;
  font-family: "PTSansRegular";
}

.clock {
  display: flex;
  justify-content: space-between;
}

.statistics_range {
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
}

.statistics_text_container {
  padding: 5px;
}

.statistics_text {
  text-align: center;
  font-size: 16px;
  color: #023047;
  font-family: "PTSansRegular";
  font-weight: bold;
}

.statistics_link {
  text-decoration: underline;
}

.statistics_report_container {
  width: 100vw;
  max-height: 500px;
  padding: 0 auto;
  color: #023047;
  display: inline-block;
  text-align: center;
}

.statistics_report_card {
  max-width: 300px;
  color: #023047;
}

.login_text {
  color: #023047;
  font-family: "PTSansRegular";
  font-size: 20px;
  font-weight: bold;
}

.login_button {
  background-color: rgba(255, 183, 3, 0.8);
  color: #023047;
}

.login_input {
  max-width: 230px;
}

.footer {
  margin-top: 10px;
}

.footer_form {
  height: 50px;
  margin-bottom: 1%;
}
